<template>
  <div>
   <van-nav-bar
   :fixed="true"
      title="任务上传"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>

    <div class="content">
      <div class="headerCont border-radius">
        <div class="flexContTitle">
          <div class="imgSize">
            <!-- <img src="../../../static/img/newData.png" alt /> -->
          </div>
          <div class="taskTitle">
            <span class="borderLeft">最新任务列表</span>
          </div>
        </div>
        <van-swipe-cell v-for="(item,index) in TaskList" :key="index">
          <!-- CarTasks -->
          <!-- <van-cell
            :border="false"
            :title="item.vehicleId"
            :label="item.gmtCreate"
            @click="btnNextNavigate"
            class="borderBottom"
            style="height: 40px;border-radius: 10px;"
          />-->
          <van-cell-group style="border-radius: 10px;" @click="btnNextNavigate">
            <van-cell :title="item.vehicleId" :label="item.gmtCreate" style="border-radius: 10px;" />
          </van-cell-group>
          <template slot="right">
            <van-button @click="closeThis(index)" square type="danger" text="删除" class="flex" />
          </template>
        </van-swipe-cell>
      </div>
      <!-- </div> -->
      <!-- <div class="border"></div>
      <div class="headerCont">
        <div class="flexContTitle">
          <div class="taskTitle">
            <span class="borderLeft">历史任务列表</span>
          </div>
        </div>
        <div>
          <div class="title">
            <div>时间</div>
            <div>任务类型</div>
            <div>车辆</div>
            <div>任务详情</div>
          </div>
          <div class="taskList" v-for="(item,index) in TaskHistoryList" :key="index">
            <div>{{item.gmtCreate.slice(0,10)}}</div>
            <div>移动监测</div>
            <div>{{item.vehicleId}}</div>
            <div class="btn" @click="btnTo(item.routingName)">
              <span style>查看</span>
            </div>
          </div>
          <van-pagination
            v-model="currentPage"
            :total-items="125"
            :show-page-size="3"
            force-ellipses
            @change="changePage"
          />
        </div>
      </div> -->
    </div>
    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-list-item></mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import { Dialog } from "vant";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
// import Vue from 'vue';
const h = document.documentElement.clientHeight || document.body.clientHeight;
const heightDevice = Math.floor((h - 320) / 40)-3;
export default {
  data() {
    return {
      status: this.$store.state.taskStatus,
      TaskList: [],
      TaskHistoryList: [
        {
          gmtCreate: "2019-09-01 11:00:00"
        }
      ],
      token: "",
      currentPage: 1,
      open: false
    };
  },
  methods: {
    // 筛选按钮
    queryBtn() {
      this.open = true;
    },
    closeBottomSheet() {
      this.open = false;
    },
    // 历史任务数据的列表
    async initHistoryList() {
      var url = "/mobile/routing";
      var endTime = "";
      var startTime = "";
      var pageNum = this.currentPage;
      var pageSize = heightDevice - 1;
      var vehicleId = this.$route.query.carId;
      var res = await AppBace.request(
        url,
        "GET",
        {
          endTime,
          startTime,
          vehicleId,
          pageNum,
          pageSize
        },
        this.token
      );
      console.log(res);
      this.TaskHistoryList = res.data.data.rows;
    },
    changePage(value) {
      console.log(value);
      this.currentPage = value;
      this.initHistoryList();
    },
    btnTo(e) {
      this.$router.push({
        path: "/carMapLine",
        query: {
          routingName: e
        }
      });
    },
    btnNextNavigate() {
      this.$router.push({
        path: "/CarTasks",
        query: {
          taskId: 1
        }
      });
    },
    closeThis(index) {
      var _this = this;
      Dialog.confirm({
        title: "",
        message: "确认删除当前创建的线路？"
      })
        .then(index => {
          // on confirm
          // console.log(routingName);
          _this.TaskList.splice(index, 1);

          _this.deleteData();
          _this.$store.state.taskStatus = 0;
          _this.$router.go(-1);
        })
        .catch(() => {
          // on cancel
        });
    },
    async deleteData() {
      var _this = this;
      console.log("routingName", this.$route.query.id);
      var url = "/mobile/routing";
      var routingName = this.$route.query.id;
      var param = { routingName: routingName };
      // var res = await AppBace.Deletedata(url,"DELETE",{routingName},this.token);
      this.$http
        .delete(url, {
          params: param
        })
        .then(res => {
          console.log(res);
          _this.status = 0;
          if (res.data.message === "success") {
            Toast("当前路线已经删除,请返回重新穿建路线");
            _this.$store.state.taskStatus = 0;
          }
        })
        .catch(err => {
          console.log(err);
        });
      // console.log(res);
    },
    onClose(clickPosition, instance, detail) {
      switch (clickPosition) {
        case "left":
        case "cell":
        case "outside":
          instance.close();
          break;
        case "right":
          Dialog.confirm({
            message: "确定删除吗？"
          }).then(res => {
            console.log(instance);
          });
          break;
      }
    },
    async initData() {
      console.log(this.$route.query.carId);
      // var url = "/mobile/point/data?vehicleId="+this.$route.query.carId;
      var url = "/mobile/point/data";
      var vehicleId = this.$route.query.carId;
      // debugger
      var res = await AppBace.request(url, "get", { vehicleId }, this.token);
      // this.$http
      //   .get(url, {vehicleId:this.$route.query.carId})
      //   .then(res => {
      //     console.log(res);
      //   })
      //   .catch(err => {
      //     console.log(err);
      //   });
      console.log("res", res);
      if (res.data.message === "success") {
        this.$store.commit("enterpriseData", res.data.pollutantSource);
      }
    },
    filterData(res, e) {
      console.log(e);
      for (let index = 0; index < res.length; index++) {
        var data = res[index].routingName;
        if (e === data) {
          console.log(res[index]);

          return res[index];
        }
      }
    }
  },
  async mounted() {
    this.TaskList = [];
    this.token = sessionStorage.getItem("token");
    this.initData();
    if (this.$store.state.taskStatus === 1) {
      // console.log(this.$store.state.taskStatus);
      // debugger;
      // 未执行完的路线
      this.TaskList = [];
      this.TaskList.push(this.$store.state.onlineTask);

      // debugger;
    } else {
      // 执行完成之后的数据,新创建的数据
      // this.TaskList = this.$store.state.onlineTask;
      var arr = [];
      var routingName = this.$route.query.id;
      var url = "/mobile/routing/app";
      var res = await AppBace.request(url, "get", {}, this.token);
      console.log(res);
      arr = await this.filterData(res.data.data, routingName);
      this.TaskList.push(arr);
      console.log("list", arr);
      this.$store.state.taskStatus = 1;
      // this.TaskList = arr;
      // debugger
    }
    setTimeout(() => {
      this.initHistoryList();
    }, 1000);
    // Notify("车辆准备出库，请谨慎驾驶车辆，遵守交规行驶，谢谢！");
    // Notify({ type: 'warning', message: '车辆准备出库，请谨慎驾驶车辆，遵守交规行驶，谢谢！',duration: 5000 });
  }
};
</script>
<style scoped>
.title {
  display: flex;
  flex-direction: row;
  height: 50px;
  /* justify-content: center; */
  align-items: center;
  border-bottom: 1px solid #ccc;
}
.title div {
  width: 25%;
  display: flex;
  justify-content: center;
  /* font-weight: 600; */
}
.taskList {
  display: flex;
  flex-direction: row;
  height: 50px;
  align-items: center;
  border-bottom: 1px solid rgb(240, 240, 240);
  color: rgb(70, 60, 60);
}
.taskList div {
  width: 25%;
  display: flex;
  justify-content: center;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.content {
  margin-top: 46px;
  padding: 10px 5px;
  /* background-color: white; */
}
.btn span {
  border-bottom: 1px solid #269af1;
  color: #269af1;
  background-color: white;
  padding: 1px;
}
.icon img {
  width: 24px;
  height: 24px;
}
.topBorder {
  border-bottom: 10px rgb(234, 236, 236) solid;
}
.taskTitle {
  /* border-left: 5px #ccc solid; */
  margin-left: 5px;
  padding-left: 5px;
}
.flexContTitle {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 60px;
  /* margin-top: 10px; */
  border-bottom: 1px solid rgb(240, 240, 240);
}
.imgSize img {
  width: 24px;
  height: 24px;
}
.borderBottom {
  border-bottom: 1px solid rgb(224, 223, 223);
  height: 40px;
}
.border {
  border-top: 10px rgb(235, 236, 236) solid;
}
.headerCont {
  /* border-radius: 10px; */
  background: white;
}
.flex {
  height: 65px;
  border-radius: 5px;
}
.borderLeft {
  border-left: 3px solid rgb(53, 114, 254);
  padding-left: 5px;
}
</style>


